.home-header{
    text-align: center;
    margin: 20px;
    padding: 10px;
    font-family: sans-serif;
    border: solid gold 3px;
}
.home-container{
    position: relative;
    width: 350px;
    height: 350px;
    display: inline-block;
    margin: 20px;
}

.image{
    display: block;
    width: 350px;
    height: 350px;
}
.overlay{
    position: absolute;
    transition: all .6s ease;
    opacity: 0;
    background-color: #333;
   
}
.home-container:hover .overlay{
    opacity: .65;
 
}
.text{
    color: white;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    
}
