.card {
    background: lightgrey;
    border-radius: 6px;
    height: 400px;
    margin: 1rem;
    display: inline-block;
    width: 350px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
  }
  
  .card > .img-container {
    height: 210px;
    overflow: hidden;
    text-align: center;
   
  }
  .card > .img-container > img {
    width: 100%;
    height: 250px;
    margin-top: 2px;
  }
  
  .card > .content {
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 13px;
   
  }
  
  .card > .content > ul {
    list-style-type: none;
  }
